.select-styled {
  position: absolute; 
  top: -2px;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 8px 15px;
  transition: 0.3s ease-in-out;

  &:after {
    content:"";
    content: "";
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 7px 4px 3px;
    border-color: #000 transparent transparent transparent;
    position: absolute;
    top: 16px;
    right: 10px;
  }
 
  &:active, &.active {
   
    &:after {
      top: 16px;
    border-color: #000 transparent transparent transparent;      
    }
  }
}

