// table-infor
.table__infor{
  margin-top: 10px;
  position: absolute;
  width: 100%;
  opacity: 0;
  transition: 0.3s ease-in-out;
  z-index: -1;
  table{
    background: #f5f5f5;
    border-radius: 5px;
    padding: 15px 0;
    width: 100%;
    tr td{
      width: 25%;
      padding: 10px 0;
      padding-left: 40px;
      p{
        margin: 0;
      }
    }
    tr:nth-child(2){
      td:nth-child(1){
        span:nth-child(2){
          text-transform: capitalize;
        }
      }
    }
    tr:nth-child(4){
      td:nth-child(1){
        span:nth-child(2){
          text-transform: capitalize;
        }
      }
      td:nth-child(2){
        span:nth-child(1){
          text-transform: capitalize;
        }
      }
    }
  }
}
