/*********************
    - 2. General -
*********************/

html,
body {
  margin: 0px;
  padding: 0px;
}

.list-inline {
  margin: 0px;
  padding: 0px;
  list-style: none;
}

.list-inline li {
  @include dis(middle);
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  border: none;
}



.fleft {
  float: left;
}

.fright {
  float: right;
}

.clear-fix,
.clearfix {
  clear: both;
}
.container{
  max-width: 1170px;
  margin: 0 auto;
}

.col50{
  width: 50%;
  box-sizing: border-box;
  padding: 0px 15px;
  float:left;
}

.col33{
  width: 33%;
  float: left;
}
.row{
  margin: 0px -15px;
}
section{
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  line-height: 25px;
}
.text-left{
  text-align: left;
}


.text-right{
  text-align: right;
}


.text-center{
  text-align: center;
}
