.popuptext {
  visibility: hidden;
  width: 160px;
  background-color: #484d50d4;
  color: #fff;
  text-align: center;
  -webkit-border-radius: 6px;
  border-radius: 6px;
  position: absolute;
  z-index: 1;
  bottom: -49%;
  left: -41%;
  margin-left: -80px;
  margin-top: 0px;
  margin-bottom: 0px;
}

.popuptext ul {
  width: 100%;
  margin: 0px;
  padding: 0px;
  list-style: none;
}

.popuptext ul li {
  display: block;
  border-bottom: 1px solid #a1a5a8;
}

.popuptext ul li:last-child {
  border-width: 0px;
}

.popuptext ul li a {
  display: block;
  padding: 10px 10px;
  color: #fff;
  width: 100% !important;
  line-height: 9px;
}

.popuptext ul li a:hover {
  text-decoration: none;
}

.popuptext::after {
  content: "";
  position: absolute;
  top: -23%;
  left: 88%;
  margin-left: -4px;
  border-width: 7px;
  border-style: solid;
  border-color: #484d50d4 transparent transparent transparent;
  transform: rotateX(180deg);
}

/* Toggle this class - hide and show the popup */
.show {
  visibility: visible;
  -webkit-animation: fadeIn 0.5s;
  animation: fadeIn 0.5s;
}

/* Add animation (fade in the popup) */
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

